import { Form, Input, Modal } from 'antd';
import ModalSendLeader from 'app/views/GeneralSettings/ModalSendLeader';
import React, { useState } from 'react';

function ResignationApplicationModal({ open, onCancel, employeeInfo, openUpdateStatus }) {
    const [sendLeader, setSendLeader] = useState(false);
    const [form] = Form.useForm();

    const handleSendLeader = () => {
        setSendLeader(!sendLeader);
    };

    const handleClear = () => {
        onCancel();
        openUpdateStatus();
    };

    return (
        <Modal
            className="information_modal general___modal"
            centered
            okText="Trnh lnh o"
            title="Biu mu"
            cancelText="HY"
            open={open}
            onOk={handleSendLeader}
            onCancel={onCancel}
            width={'70vw'}
        >
            <Form form={form} className="use-font-time">
                <div className="modal_resignation-top">
                    <h4>Cng HA X HI CH NGHA VIT NAM</h4>
                    <p>c lp - T do - Hnh Phc</p>
                </div>
                <div className="modal_resignation-content">
                    <h4>n xin ngh vic</h4>
                    <p className="content-general">Knh gi: Ban Gim c cng ty OceanTech</p>

                    <div className="content_top">
                        <Form.Item name="fullName" label="Ti tn l: ">
                            <Input defaultValue={employeeInfo.fullName} />
                        </Form.Item>
                    </div>
                    <div className="content_top">
                        <Form.Item label="Hin ang cng tc ti v tr: ">
                            <Input defaultValue={employeeInfo.registerPosition} />
                        </Form.Item>
                    </div>

                    <p>
                        Ti rt ly lm tic v s khng cn lm vic ti Cng ty OceanTech trong thi gian sp ti. Ti
                        quyt inh nhn v tr cng vic mi  tm thm c hi pht trin mi, ph hp vi chuyn mn
                        v kh nawg lm vic ca ti.
                    </p>
                    <p>
                        Ti thc s ly lm vinh d ln c lm vic ti y trong sut X nm. Trong qu trnh lm vic
                        ti y, Cng ty  cho ti c c may mn lm vic vi nhng ng nghip tt cng vi mt mi
                        trng lm vic tt. Ti xin chn thnh cm n Cng ty  tin tng ti trong sut thi gian va
                        qua v chc cho Cng ty chng ta s t c nhng thnh cng nh mong mun.
                    </p>

                    <p>
                        Vy ti lm n ny xin c ngh vic t ngy 01-05-2023 Ti s bn giao cng vic ca mnh mt
                        cch y  v c trch nhim.
                    </p>
                    <p>Rt mong c s gip  ca Ban lnh o Cng ty.</p>

                    <div className="content-end">
                        <p>Ti xin chn thnh cm n!</p>
                    </div>
                </div>

                <div className="content_bottom">
                    <p>H ni, Ngy 20 thng 04 nm 2023</p>
                </div>
                <div className="information_modal-bottom">
                    <div className="information_modal-bottom--left">
                        <p> kin ca b phn</p>
                        <p className="font-s-i font-z-13">Tech</p>
                        <p>OceanTech</p>
                    </div>
                    <div className="information_modal-bottom--right">
                        <p>Ngi lm n</p>
                        <p className="font-s-i font-z-13">{employeeInfo.fullName.split(' ').slice(-1).join(' ')}</p>
                        <p>{employeeInfo.fullName}</p>
                    </div>
                </div>
            </Form>

            {sendLeader && (
                <ModalSendLeader
                    open={sendLeader}
                    onCancel={handleSendLeader}
                    employeeId={employeeInfo?.employeeId}
                    onClose={handleClear}
                    status={8}
                />
            )}
        </Modal>
    );
}

export default ResignationApplicationModal;
