
import React from "react";
import { Button, Col, Form, Image, Input, Row, Select, Upload } from "antd";

const { Option } = Select;

const getBase64 = (img, callback) => {
    const reader = new FileReader();
    reader.addEventListener("load", () => callback(reader.result));
    reader.readAsDataURL(img);
};

const beforeUpload = (file) => {
    const isJpgOrPng = file.type === "image/jpeg" || file.type === "image/png";
    if (!isJpgOrPng) {
        alert("You can only upload JPG/PNG file!");
    }
    const isLt2K = file.size / 1024 < 5;
    if (!isLt2K) {
        alert("Hnh nh phi c kch thc nh hn 5Kb");
    }
    return isJpgOrPng && isLt2K;
}

const TabStaffInformation = ({ listGender, fileImage, setFileImage }) => {
    const listTeam = [
        { value: 1, label: "Back-End" },
        { value: 2, label: "Front-End" },
        { value: 3, label: "Tester" }
    ];

    const handleChange = (info) => {
        if (info.file.status === "uploading") {
            return;
        }
        if (info.file.status === "done") {
            getBase64(info.file.originFileObj, (url) => {
                setFileImage(url);
            });
        }
    };

    return (
        <Row justify="space-between">
            <Col span={16}>
                <Row gutter={[16, 0]}>
                    <Col className="gutter-row" span={8}>
                        <Form.Item
                            label='Tn nhn vin'
                            name="fullName"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bt buc phi nhp trng ny!',
                                },
                                {
                                    pattern: '^([a-zA-Z ])*$',
                                    message: 'Ch cha ch khng du',
                                },
                            ]}
                        >
                            <Input style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>
                    <Col className="gutter-row" span={8}>
                        <Form.Item
                            label="M nhn vin"
                            name="code"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bt buc phi nhp trng ny!',
                                },
                                { whitespace: true, message: 'khng c  trng' },
                                { min: 6, message: 'Ti thiu 6 k t', },
                                { max: 10, message: 'Ti a 10 k t' }
                            ]}
                        >
                            <Input style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>
                    <Col className="gutter-row" span={8}>
                        <Form.Item
                            name="gender"
                            label="Gii tnh"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bt buc phi chn trng ny!',
                                },
                            ]}
                        >
                            <Select
                                placeholder="Gii tnh"
                            >
                                {listGender.map(item => {
                                    return <Option key={item.value} value={item.value}>{item.label}</Option>;
                                })}
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col className="gutter-row" span={8}>
                        <Form.Item
                            label="Ngy sinh"
                            name="dateOfBirth"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bt buc phi chn trng ny!',
                                },
                            ]}
                        >
                            <Input type="date" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>
                    <Col className="gutter-row" span={8}>
                        <Form.Item
                            label="S in thoi"
                            name="phone"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bt buc phi nhp trng ny!',
                                },
                                { min: 10, message: 'Ti thiu 10 s' },
                                { max: 11, message: 'Ti a 11 s' },
                                {
                                    pattern: '^([0-9][0-9]*|0)$',
                                    message: 'Trng ny phi l s',
                                }
                            ]}
                        >
                            <Input style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col className="gutter-row" span={8}>
                        <Form.Item
                            label='Nhm'
                            name="teamId"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bt buc phi chn trng ny!',
                                },
                            ]}
                        >
                            <Select
                                placeholder="Chn nhm"
                            >
                                {listTeam?.map(item => {
                                    return <Option key={item.value} value={item.value}>{item.label}</Option>;
                                })}
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col className="gutter-row" span={12}>
                        <Form.Item
                            label='M CCCD'
                            name="citizenId"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bt buc phi nhp trng ny!',
                                },
                                {
                                    pattern: '^([0-9][0-9]*|0)$',
                                    message: 'Trng ny phi l s',
                                },
                                { min: 9, message: 'Ti thiu 9 s' },
                                { max: 12, message: 'Ti a 12 s' },
                            ]}
                        >
                            <Input style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>
                    <Col className="gutter-row" span={12}>
                        <Form.Item
                            label="Email"
                            name="email"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bt buc phi nhp trng ny!',
                                },
                                {
                                    type: 'email',
                                    message: 'Khng ng nh dng email!',
                                }
                            ]}
                        >
                            <Input style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col className="gutter-row" span={24}>
                        <Form.Item
                            label='a ch c th'
                            name="address"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bt buc phi nhp trng ny!',
                                },
                                { whitespace: true, message: 'khng c  trng' },
                            ]}
                        >
                            <Input style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                </Row>
            </Col>
            <Col span={8} className="image-info">
                <Row>
                    <div className="avatar" >
                        <Image
                            src={fileImage ? fileImage?.toString() : "error"}
                            fallback="data:image/png;base64,"
                        />
                    </div>
                </Row>
                <Row >
                    <Form.Item name="photoUrl">
                        <Upload
                            name="photoUrl"
                            action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                            onChange={handleChange}
                            beforeUpload={beforeUpload}
                        >
                            <Button >
                                Thm nh i din
                            </Button>
                        </Upload>
                    </Form.Item>
                </Row>
            </Col>
        </Row >
    )
}
export default TabStaffInformation;