import React from 'react';
import { Form, Image, Input, Table } from 'antd';
import moment from 'moment';

function FormCurriculumVitae(props) {
    const { data, familyRelations, disabled } = props;

    const columns = [
        {
            title: 'STT',
            render: (text, record, index) => index + 1,
            width: '5%',
            align: 'center',
        },
        {
            title: 'Quan h',
            dataIndex: 'relation',
            width: '9%',
            align: 'center',
        },
        {
            title: 'H v tn',
            dataIndex: 'name',
            width: '15%',
            align: 'center',
        },
        {
            title: 'Ngy sinh',
            render: (rowData) => moment(rowData.dateOfBirth, 'YYYY-MM-DD').format('DD-MM-YYYY'),
            width: '12%',
            align: 'center',
        },
        {
            title: 'Gii tnh',
            render: (record) => (record.gender === 0 ? 'Nam' : 'N'),
            width: '10%',
            align: 'center',
        },
        {
            title: 'S CCCD/CMT',
            dataIndex: 'citizenId',
            width: '15%',
            align: 'center',
        },
        {
            title: 'a ch',
            dataIndex: 'address',
            width: '24%',
            align: 'center',
        },
    ];

    return (
        <div className="curriculum_vitae use-font-time">
            <div className="curriculum_vitae-top">
                <div className="curriculum-image">
                    <Image
                        src={data?.photoUrl ? data?.photoUrl : 'error'}
                        fallback="data:image/png;base64,"
                    />
                </div>

                <div>
                    <div className="flexCenter">
                        <h4 className="font-z-18">CNG HA X HI CH NGHA VIT NAM</h4>
                        <h5>
                            <span className="font-z-18 border-bt">c lp - T do - Hnh Phc</span>
                        </h5>
                        <h3 className="font-z-22 font-w mt-40">S YU L LCH</h3>
                        <h3 className="font-z-16 font-w">T THUT</h3>
                    </div>
                </div>
            </div>

            <div className="mt-30">
                <h4 className="font-z-18">I. THNG TIN BN THN</h4>
                <div className="pl-15">
                    <div className="curriculum_vitae-info">
                        <div className="flex-1">
                            <Form.Item label="1. H v tn: ">
                                <Input disabled defaultValue={data?.fullName} />
                            </Form.Item>
                        </div>
                        <div className="w-25">
                            <Form.Item label="Nam, n: ">
                                <Input disabled defaultValue={data?.gender === 0 ? 'Nam' : 'N'} />
                            </Form.Item>
                        </div>
                    </div>

                    <div className="curriculum_vitae-info">
                        <div className="w-16">
                            <Form.Item label="2. Sinh ngy: ">
                                <Input disabled defaultValue={moment(data?.dateOfBirth).format('DD')} />
                            </Form.Item>
                        </div>
                        <div className="w-12">
                            <Form.Item label="thng ">
                                <Input disabled defaultValue={moment(data?.dateOfBirth).format('MM')} />
                            </Form.Item>
                        </div>
                        <div className="w-12">
                            <Form.Item label="nm ">
                                <Input disabled defaultValue={moment(data?.dateOfBirth).format('YYYY')} />
                            </Form.Item>
                        </div>
                        <div className="flex-1">
                            <Form.Item label="Ni sinh: ">
                                <Input disabled defaultValue={data?.address} />
                            </Form.Item>
                        </div>
                    </div>

                    <div>
                        <Form.Item name="currentAddress" label="3. Nguyn qun: ">
                            <Input disabled={disabled} defaultValue={data?.currentAddress} />
                        </Form.Item>
                    </div>

                    <div className="curriculum_vitae-info">
                        <div className="w-25">
                            <Form.Item label="4. S CCCD: ">
                                <Input disabled defaultValue={data?.citizenId} />
                            </Form.Item>
                        </div>
                        <div className="w-25">
                            <Form.Item name="citizenIdIssuanceDate" label="Cp ngy: ">
                                <Input disabled={disabled} />
                            </Form.Item>
                        </div>
                        <div className="flex-1">
                            <Form.Item name="citizenIdIssuingAuthority" label="Ni cp: ">
                                <Input disabled={disabled} defaultValue={data?.citizenIdIssuingAuthority} />
                            </Form.Item>
                        </div>
                    </div>

                    <div>
                        <Form.Item label="5. S in thoi lin h: ">
                            <Input disabled defaultValue={data?.phone} />
                        </Form.Item>
                    </div>

                    <div>
                        <Form.Item label="6. Email: ">
                            <Input disabled defaultValue={data?.email} />
                        </Form.Item>
                    </div>
                    <div className="curriculum_vitae-religion">
                        <div className="w-50">
                            <Form.Item name="ethnicity" label="7. Dn tc: ">
                                <Input disabled={disabled} defaultValue={data?.ethnicity} />
                            </Form.Item>
                        </div>

                        <div className="w-50">
                            <Form.Item name="religion" label="8. Tn gio: ">
                                <Input disabled={disabled} defaultValue={data?.religion} />
                            </Form.Item>
                        </div>
                    </div>
                </div>
            </div>

            <div className="mt-20">
                <h4 className="mb-15 font-z-18">II. QUAN H GIA NH</h4>
                <p className="m-0 text-ident-30 font-s-i">
                    Ghi r h tn, nm sinh, ngh nghip, ni cng tc ca b m , anh ch em rut, v(hoc chng),
                    con
                </p>

                <Table
                    dataSource={familyRelations}
                    className="table-information table-family"
                    bordered
                    size="small"
                    columns={columns}
                    pagination={false}
                />
            </div>

            <div>
                <p className="text-ident-30">
                    Ti xin cam oan bn khai s yu l lch trn ng s tht, nu c iu g khng ng ti chu trch
                    nhim trc php lut v li khai ca mnh.
                </p>
                <div className="modal_signature">
                    <p>
                        <span>H ni, </span>
                        <span>ngy 20 </span>
                        <span>thng 04 </span>
                        <span>nm 2023</span>
                    </p>
                    <p>Ngi khai</p>
                    <Form.Item name="commonName">
                        <Input
                            disabled={disabled}
                            placeholder="nhp ch k"
                            defaultValue={data?.commonName}
                            className="curriculum_vitae-common"
                        />
                    </Form.Item>
                    <Form.Item>
                        <Input disabled className="modal_signature-fullName" defaultValue={data?.fullName} />
                    </Form.Item>
                </div>
            </div>
        </div>
    );
}

export default FormCurriculumVitae;
