import { Button, Form, Input, Modal } from 'antd';
import React from 'react';
import moment from 'moment';

function SalaryIncreaseModal({ open, onCancel, employeeInfo, staffDataSalary, onSend }) {
    return (
        <Modal
            className="information_modal general___modal"
            centered
            title="Biu mu"
            open={open}
            onCancel={onCancel}
            width={900}
            footer={[
                <Button onClick={onCancel} key="1">
                    Hy
                </Button>,
                <Button type="primary" onClick={onSend} key="2">
                    Trnh lnh o
                </Button>,
                <Button key="3" type="primary" className="btn-save">
                    Lu
                </Button>,
            ]}
        >
            <Form className="use-font-time">
                <div className="form-information-republic font-z-13">
                    <h4 className="form-information-title">CNG HA X HI CH NGHA VIT NAM</h4>
                    <p>
                        <span>c lp - T do - Hnh Phc</span>
                    </p>
                </div>
                <div className="modal_content">
                    <h3 className="modal_content-title">QUYT NH</h3>
                    <p className="modal_content-text">V vic iu chnh thng chc cho Nhn Vin</p>
                    <div>
                        <Form.Item name="regulations" label="- Cn c ti quy ch, iu l Cng ty: ">
                            <Input defaultValue="OceanTech" />
                        </Form.Item>
                    </div>
                    <p>- Cn c vo hp ng lao ng vi ngi lao ng</p>
                    <p>Xt nhng ng gp ca ngi lao ng v  ngh ca trng phng nhn s</p>
                    <h3 className="modal_content-title">GIM C QUYT NH</h3>
                    <div className="modal_content-body">
                        <Form.Item name="dateSalary" label="iu 1: k t ngy ">
                            <Input defaultValue={moment(staffDataSalary?.date).format('DD-MM-YYYY')} />
                        </Form.Item>
                        <Form.Item name="fullName" label="iu chnh mc lng ca ng/B ">
                            <Input defaultValue={employeeInfo.fullName} />
                        </Form.Item>
                        <Form.Item name="rank" label="s l bc ">
                            <Input defaultValue={staffDataSalary?.salaryScale} />
                        </Form.Item>
                    </div>
                    <p>
                        iu 2: Quyt nh ny c hiu lc k t ngy k. Cc ng Trng Phng, Phng Hnh Chnh - Tng
                        hp v cc ng c tn trn cn c vo quyt nh ny  thi hnh.
                    </p>
                </div>
                <div className="modal_signature">
                    <div className="date-signature font-s-i">H ni, ngy 20 thng 04, 2023.</div>
                    <p>TNG GIM C</p>
                    <p className="font-s-i font-z-13">Tech</p>
                    <p>OceanTech</p>
                </div>
            </Form>
        </Modal>
    );
}

export default SalaryIncreaseModal;
