import { Modal } from 'antd';
import React from 'react';

const ModalDelete = ({ open, onCancel, onOk }) => {
    return (
        <Modal
            title="Xa bn ghi"
            centered
            open={open}
            width={500}
            okText="Xc nhn"
            cancelText="Hy"
            onCancel={onCancel}
            onOk={onOk}
            className="modal-delete"
        >
            <p>Bn c chc chn mun xa?</p>
        </Modal>
    );
};

export default ModalDelete;
