import { Button, Form, Input, Modal } from 'antd';
import React from 'react';
import { useState } from 'react';

function ProposedModal({ open, onCancel, employeeInfo, ProposedList, onSend }) {
    const [dataForm, setDataForm] = useState({
        fullName: employeeInfo?.fullName || '',
    });

    const onChangeValue = (e) => {
        setDataForm({ ...dataForm, [e.target.name]: e.target.defaultValue });
    };

    return (
        <Modal
            className="information_modal general___modal"
            centered
            title="Biu mu"
            open={open}
            onCancel={onCancel}
            width={'70vw'}
            footer={[
                <Button onClick={onCancel} key="1">
                    Hy
                </Button>,
                <Button type="primary" onClick={onSend} key="2">
                    Trnh lnh o
                </Button>,
                <Button key="3" className="btn-save" type="primary">
                    Lu
                </Button>,
            ]}
        >
            <div className="use-font-time">
                <div className="form-information-header font-z-13">
                    <h4>Cng HA X HI CH NGHA VIT NAM</h4>
                    <p>
                        <span>c lp - T do - Hnh Phc</span>
                    </p>
                </div>
                <div className="modal_content">
                    <h4 className="modal_content-title title-proposed">N  XUT  KIN THAM MU</h4>
                    <p>Knh gi: Ban Gim c cng ty OceanTech</p>

                    <Form.Item name="name" onChange={onChangeValue} label="Ti tn l: ">
                        <Input defaultValue={employeeInfo.fullName} />
                    </Form.Item>
                    <Form.Item name="location" onChange={onChangeValue} label="Hin ang cng tc ti v tr: ">
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name="problem"
                        onChange={onChangeValue}
                        label="Ti vit n ny  ngh  Cng ty xem xt v gii quyt vn : "
                    >
                        <Input defaultValue={ProposedList.type} />
                    </Form.Item>
                    <Form.Item name="date" onChange={onChangeValue} label="K t ngy: ">
                        <Input defaultValue={ProposedList.date} />
                    </Form.Item>
                    <div className="form_block">
                        <Form.Item name="reason" onChange={onChangeValue} label="Ni dung nh sau: ">
                            <Input defaultValue={ProposedList.content} />
                        </Form.Item>
                    </div>

                    <p>
                        Knh mong Cng ty / c nhn c thm quyn xem xt n  ngh v gii quyt vn  m ti  nu
                         trn.
                    </p>
                    <p>
                        Ti xin cam oan nhng thng tin trn hon ton ng s tht, nu c g sai st ti xin chu
                        trch nhim trc Cng ty v trc php lut.
                    </p>
                    <p className="text-ident font-w">Ti xin chn thnh cm n!</p>
                </div>
                <div className="modal_signature">
                    <div className="date-signature font-s-i">H ni, ngy 20 thng 04, 2023.</div>
                    <p>Ngi lm n</p>
                    <p className="font-s-i font-z-13">{employeeInfo.fullName.split(' ').slice(-1).join(' ')}</p>
                    <p>{employeeInfo.fullName}</p>
                </div>
            </div>
        </Modal>
    );
}

export default ProposedModal;
